import React from 'react';
import { useItemState } from '../ItemContext';
import * as CommonUtils from '../CommonUtils.js';
import MyForm from './DayPicker.js';

function Head(){
    const items = useItemState();
    
    let volumn = 0;

    items.forEach(element => {
        if(element.type === "in"){
            volumn += Number(CommonUtils.Format_NoComma(element.value));
        }else{
            volumn -= Number(CommonUtils.Format_NoComma(element.value));
        }
    });

    console.log(items);

    const today = new Date();
    const dayName = today.toLocaleDateString('ko-KR', { weekday: 'long'});

    return (
        <div className="Head">
            <div><MyForm /></div>
            <div className="tasks-left">잔액 {CommonUtils.Format_comma(volumn)} 원</div>
        </div>
    )
}

export default Head;